import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Card } from '@/components/ui/card'


export default function ContactSection() {
   const onSubmit = async (event) => {
    event.preventDefault();
    const formData = new FormData(event.target);

    formData.append("access_key", "gbqtqvzu6mz13w");

    const object = Object.fromEntries(formData);
    const json = JSON.stringify(object);

    const res = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      },
      body: json
    }).then((res) => res.json());

    if (res.success) {
      console.log("Success", res);
    }
  };
    return (
        <section className="py-32  px-4 lg:px-8">
            <div className="mx-auto max-w-3xl hover:border-purple-500 px-8 lg:px-0">
                <h1 className="text-center text-purple-600 text-4xl font-semibold lg:text-5xl">Contact Reevio</h1>
                <p className="mt-4 text-center">We'll help you find the right plan and pricing for your videos.</p>

                <Card className="mx-auto mt-12 max-w-lg p-8 shadow-md sm:p-16">
                    <div>
                        <h2 className="text-xl font-semibold">Let's get you to the right place</h2>
                        <p className="mt-4 text-sm">Reach out to our Reevio team! Weâ€™re eager to learn more about how you plan to use our services.</p>
                    </div>

                    <form
                        action=""
                        className="**:[&>label]:block mt-12 space-y-6 *:space-y-3">
                        <div>
                            <Label htmlFor="name">Full name</Label>
                            <Input
                                type="text"
                                id="name"
                                required
                            />
                        </div>

                        <div>
                            <Label htmlFor="email"> Email Id</Label>
                            <Input
                                type="email"
                                id="email"
                                required
                            />
                        </div>

                        <div>
                            <Label htmlFor="country">Plan Selection</Label>
                            <Select>
                                <SelectTrigger>
                                    <SelectValue placeholder="Select your plan" />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="1">Basic</SelectItem>
                                    <SelectItem value="2">Pro</SelectItem>
                                    <SelectItem value="3">Premium</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>

                        <div>
                            <Label htmlFor="msg">Message</Label>
                            <Textarea
                                id="msg"
                                rows={3}
                            />
                        </div>

                        <Button onClick={onSubmit}>Submit</Button>
                    </form>
                </Card>
            </div>
        </section>
    )
}
